.Image {
  height: 40vmin;
  padding-right: 3vmin;
  padding-left: 3vmin;
  pointer-events: none;
}

.Top-header {
  background-color: #d04b5a;
  padding-top: 5vmin;
  padding-bottom: 5vmin;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 5vmin);
  color: white;
  text-align: center;
}

.Top-small-text {
  font-size: calc(10px + 2vmin)
}

.List-header {
  padding-left: 10vmin;
  padding-top: 4vmin;
  padding-bottom: 4vmin;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: calc(10px + 5vmin);
  color: white;
}

.List-button {
  margin-right: 10vmin;
  height: calc(10px + 6vmin);
  width: calc(10px + 6vmin);
}

@media only screen and (min-width: 1000px){
  .List-body {
    padding-left: 7vmin;
    padding-bottom: 3vmin;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
    font-weight: 100;
  }

@media only screen and (max-width: 1000px){
  .List-body {
    padding-left: 7vmin;
    padding-bottom: 3vmin;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
    font-weight: 100;
  }
}

  .List-body {
    padding-left: 7vmin;
    padding-bottom: 3vmin;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
    font-weight: 100;
  }

}